.menu-container {
  display: flex;
  align-items: center;
  justify-content: start;
  padding: 0px;
  margin: 0px;
  width: 100%;
}
.nav-links {
  width: 100%;
  height: 10%;
  list-style-type: none;
  margin: 0;
  padding: 10px;
  overflow: hidden;
  opacity: 0.5;
  float: left;
}
.nav-logo :hover {
  transform: scale(1.1);
}
.menu-container li {
  height: 100%;
  float: left;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.light {
  .navbar {
    width: 100%;
    background-color: #ffbebe;
  }

  .menu-container li a:hover {
    color: rgb(31, 186, 197);
  }

  .nav-logo {
    display: flex;
    float: right;
    margin-right: 50px;
  }

  .nav-logo img {
    width: 50px;
    height: auto;
  }

  .menu-container li a {
    display: block;
    color: rgb(0, 0, 0);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 20px;
    font-family: Poppins, sans-serif;
  }
}

.dark {
  .navbar {
    width: 100%;
    background-color: #040101;
  }
  .menu-container li a:hover {
    color: rgb(31, 186, 197);
  }

  .nav-logo {
    display: flex;
    float: right;
    margin-right: 50px;
    background-color: aqua;
    opacity: 0.4;
  }

  .nav-logo img {
    width: 50px;
    height: auto;
  }

  .menu-container li a {
    display: block;
    color: rgb(255, 255, 255);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 20px;
    font-family: Poppins, sans-serif;
  }
}
